.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.header nav span {
  color:red;
}
.span1 {
  padding: 30px;
  font-size: 25px;
  color: #666666;
}
.header div ul li {
  padding: 10px 25px;
  color:white;
}
.header ul li:hover {
  color: #f9004d;
  transition: .4s;
}
.secondlayer {
  background-image: url(../public/template4img20.png); 
  height: 100vh;
  width: 100%;
  background-position: center;
  background-size: cover;
}
.image {
  padding-top: 80px;
  padding-left: 50px;
  padding-right: 20px;
}
.content {
  display: flex;
  justify-content: space-evenly;
  padding-top: 40px;
}
.content h1 {
  color:white;
}
content p {
  color: gray;
}
.content span {
  color: #f9004d;
}
.spec {
  display:flex;
  justify-content: space-evenly;
  background-color: black;
  padding: 50px;
}
.spec div {
  color: white;
}
.spec div p {
  color: gray;
}
.spec div button {
  background-color: #f9004d;
  color: white;
  padding: 13px 30px;
}
.info {
  display: flex;
  padding: 50px;
  background-image: url(../public/template4img20.png); 
}
.info1 {
  padding-top: 20px;
  color: white;
}
.info2 {
  padding-left: 20px;
  color: white;
  padding-top: 100px;
}
.info p {
  padding-top: 5PX;
}
.info span {
  color: gray;
}
.info2 div {
  display: flex;
  justify-content: space-around;
  color: #f9004d;
}
.ul {
  padding-right: 80px;
}
.info3 {
  padding-left: 60px;
  padding-top: 20px;
  color:white;
}
.info3 img {
  padding: 10px;
  width:20%;
}
.copy1 {
  display: flex;
  justify-content: space-between;
  padding-top: 40px;
  padding-left: 120px;
  font-size: 30px;
}
.copy2 {
  display: flex;
  justify-content: space-between;
  padding-right: 70px;
}